<template>
    <div class="navbar">
        <router-link to="/goods" class="nav-item">
            点餐
            <i class="line"></i>
        </router-link>
        <router-link to="/ratings" class="nav-item">
            评价({{commentNum}})
            <i class="line"></i>
        </router-link>
        <router-link to="/seller" class="nav-item">
            商家
            <i class="line"></i>
        </router-link>
    </div>
</template>

<script>
    export default {
        name: "navbar",
        props:{
            commentNum:{
                type:Number,
                default:0
            }
        }
    }
</script>

<style scoped>
    .navbar {
        display: flex;
        width: 100%;
        height: 40px;
        line-height: 40px;
        border-bottom: 1px solid #e4e4e4;
    }

    .nav-item {
        flex: 1;
        text-align: center;
        font-size: 13px;
        text-decoration: none;
        color: #666666;
    }
/*选中样式*/
    .navbar .active{
        color:#FFBB22;
        position: relative;

    }
    .navbar .active .line{
        width: 20px;
        height: 2px;
        display: inline-block;
        background: #FFBB22;
        position: absolute;
        left: 50%;
        bottom: 0;
        margin-left: -10px;
    }
</style>


