<template>
    <div id="app">
        <!--头部信息-->
        <app-header :poiInfo="poiInfo"></app-header>
        <!--导航-->
        <app-nav :commentNum="commentNum"></app-nav>
        <!--内容-->
        <keep-alive>
        <router-view></router-view>
        </keep-alive>
    </div>
</template>

<script>
    import Header from './components/header/Header'
    import NavBar from './components/nav/NavBar'

    export default {
        name: 'App',
        components: {
            "app-header": Header,
            "app-nav": NavBar
        },
        data() {
            return {
                poiInfo: {},
                commentNum:0
            }
        },
        created() {
            fetch("https://wd8800347542ktnlww.wilddogio.com/goods.json")
                .then((res) => {
                    return res.json()
                })
                .then((res) => {
                    for(let key in res){
                        // console.log(res[key]);
                        if (res[key].code === 0) {
                            this.poiInfo = res[key].data.poi_info;
                        }
                    }

                })

            //请求ratings
            fetch("https://wd8800347542ktnlww.wilddogio.com/ratings.json")
                .then((res) => {
                    return res.json()
                })
                .then((res) => {
                    for(let key in res){
                        if (res[key].code === 0) {
                            this.commentNum = res[key].data.comment_num;
                        }
                    }

                })
        }
    }
</script>

<style>

</style>



